
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI',
		'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
    

}
@import url('https://fonts.googleapis.com/css2?family=Fasthand&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas,
		'Courier New', monospace;
}
.container{
    max-width: 950px;
    margin-right: auto;
    margin-left: auto;
    padding: 20px;
    overflow: hidden;
    min-height: 1800px;
    max-height: 100%;
     overflow: hidden;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
    background-color: #f0efef;
}
.main-dark{
    max-width: 950px;
    min-height: 1800px;
    max-height: 100%;
     overflow: hidden;
    margin-right: auto;
    margin-left: auto;
    padding: 20px;
    background-color: #212329;
}
.notes-list {
	display: grid;
	grid-gap: 1rem;
	grid-template-columns: repeat(
		auto-fill,
		minmax(250px, 1fr)
	);
}

nav {
    display: flex;
    align-items: center;
    background-color: #ffffff;
    padding: 30px 25px;
    height: 30px;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
    z-index: 1;
    justify-content: space-between;
}
nav.dark{
    background-color: #4A5667;
    color: #ffffff;
}

.left-header h1{
    margin: 0px 0px;   
    font-family: 'Bebas Neue', sans-serif;
    

    font-weight: 600;
}
.left-header p{
    margin: -5px 0px;  
    font-family: 'Fasthand', cursive; 

}
.toggler{
    display: flex;
    align-items: center;
}
.toggler p {
    font-size: 0.8rem;
    font-weight: 600;
}
.toggler--slider {
    width: 25px;
    height: 13px;
    background-color: rgb(117, 137, 223);
    border-radius: 2rem;
    margin-inline: 7px;
    cursor: pointer;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border: 1px solid rgb(117, 137, 223);
    box-sizing: content-box;
}
.toggler--slider--circle {
    height: 13px;
    width: 13px;
    background-color: #fecc8a;
    border-radius: 50%;
}
nav.dark .toggler--slider {
    border-color: #fecc8a;
    background-color:#fecc8a;
}
nav.dark .toggler--slider--circle {
    background-color: rgb(117, 137, 223);
}
nav.dark .toggler--slider {
    justify-content: flex-end;
}

.note.new{
    background-color: rgb(117, 137, 223);
}
textarea{
    border: none;
    resize: none;
    background-color: rgb(117, 137, 223);
    color: aliceblue;
}
textarea:focus{
    outline: none;
}
.save{
    background-color: #e1e1e1;
    border: none;
    border-radius: 15px;
    padding: 5px 10px ;
}
.save:hover{
background-color: #ededed;
cursor: pointer;
}

.note{
    background-color: #fecc8a;
    white-space: pre-wrap;
    border-radius: 10px;
    padding: 1rem;
    min-height: 170px;
    
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.note-footer{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.delete-icon{
cursor: pointer;
}
.search{
    display: flex;
    align-items: center;
    background-color: rgb(233,233,233);
    border-radius: 10px;
    padding: 5px;
    margin-bottom: 1.5rem;
    margin-top: 10px;

}
.search input{
    border: none;
    background-color: rgb(233,233,233);
}
.search input:focus{
    outline:none;
}
